<script>
// import  from './components/.vue'

export default {
  name: 'App',
  components: {},
  data() {
    return {
      data: [],
      currencyOne: 'USD',
      currencyTwo: 'EUR',
      rate: '',
      amountOne: 1,
      amountTwo: 0,
    };
  },
  methods: {
    calculResult() {
      fetch(
        `https://v6.exchangerate-api.com/v6/2c98d3e7b5230262b6a855fa/latest/${this.currencyOne}`,
      )
        .then(res => res.json())
        .then(data => {
          this.data = data;
          this.rate = data.conversion_rates[this.currencyTwo];
          this.amountTwo = this.amountOne * this.rate.toFixed(2);
        });
    },
    switchValues() {
      const temp = this.currencyOne;
      this.currencyOne = this.currencyTwo;
      this.currencyTwo = temp;
      this.calculResult();
    },
  },
  mounted() {
    this.calculResult();
  },
};
</script>

